:root {
  --primary-color: #042A2B;
  --secondary-color: #1C809B;
  --light-color: #EF7B45;
  --highlight-color: #D84727;
  --body-text-color: #17353B;

  --shadow-size: 10px;
  --shadow-at-hover: 5px;
} 

body {
  margin: 0;
  font-family: 'Trebuchet MS', sans-serif;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
